.wrapper{
    display: grid;
    grid-template-areas: 
    'mainPhoto theme '
    'answers theme';
    grid-template-rows: 1fr 0.4fr;
    grid-template-columns: 1fr 2fr;
    grid-gap: 1vw;
}
.picture{
    grid-area: mainPhoto;
    width: 130px;
    height: 140px;
}
.theme{
    grid-area: theme;
    margin-right: auto;
    padding-top: 10px;
}
.link{
    grid-area: link;
    margin-left: auto;
}
.mainNews{
    padding: 30px;

}
